// * ------------------------------------------------------------
// * Calculator
// * ------------------------------------------------------------

function calc() {
  const calcResult = document.querySelector(".calculating__result span");
  let sex, height, weight, age, ratio;

  if (localStorage.getItem("sex")) {
    sex = localStorage.getItem("sex");
  } else {
    sex = "female";
    localStorage.setItem("sex", "female");
  }

  if (localStorage.getItem("ratio")) {
    ratio = localStorage.getItem("ratio");
  } else {
    ratio = 1.375;
    localStorage.setItem("ratio", 1.375);
  }

  const calculatingResult = () => {
    if (!height || !weight || !age) {
      calcResult.textContent = "____";
      return;
    }

    if (sex === "male") {
      calcResult.textContent = Math.round(
        (88.36 + 13.4 * weight + 4.8 * height - 5.7 * age) * ratio
      );
    }
    if (sex === "female") {
      calcResult.textContent = Math.round(
        (447.6 + 9.2 * weight + 3.1 * height - 4.3 * age) * ratio
      );
    }
  };

  calculatingResult();

  const initLocalSettings = (selector, activeClass) => {
    const elems = document.querySelectorAll(selector);

    elems.forEach((elem) => {
      elem.classList.remove(activeClass);

      if (
        elem.getAttribute("id") === localStorage.getItem("sex") ||
        elem.getAttribute("data-ratio") === localStorage.getItem("ratio")
      ) {
        elem.classList.add(activeClass);
      }
    });
  };

  initLocalSettings("#gender div", "calculating__choose-item_active");
  initLocalSettings(
    ".calculating__choose_big div",
    "calculating__choose-item_active"
  );

  const getStaticData = (parentSelector, activeClass) => {
    const elems = document.querySelectorAll(`${parentSelector} div`);

    elems.forEach((elem) => {
      elem.addEventListener("click", (e) => {
        if (e.target.getAttribute("data-ratio")) {
          ratio = +e.target.getAttribute("data-ratio");
          localStorage.setItem("ratio", +e.target.getAttribute("data-ratio"));
        } else {
          sex = e.target.getAttribute("id");
          localStorage.setItem("sex", e.target.getAttribute("id"));
        }

        elems.forEach((elem) => {
          elem.classList.remove(activeClass);
        });
        e.target.classList.add(activeClass);

        calculatingResult();
      });
    });
  };

  getStaticData("#gender", "calculating__choose-item_active");
  getStaticData(".calculating__choose_big", "calculating__choose-item_active");

  const getDynamicData = (selector) => {
    const inputs = document.querySelectorAll(`${selector} input`);

    inputs.forEach((input) => {
      input.addEventListener("input", () => {
        if (input.value.match(/\D/g)) {
          input.style.border = "1px solid red";
        } else {
          input.style.border = "";
        }

        switch (input.getAttribute("id")) {
          case "height":
            height = +input.value;
            break;
          case "weight":
            weight = +input.value;
            break;
          case "age":
            age = +input.value;
            break;
        }

        calculatingResult();
      });
    });
  };

  getDynamicData(".calculating__choose_medium");
}

export default calc;
